lor:${props.app.color? props.app.color: '#151233'} !important;`\nconst block = computed(() => !props.app.synced);\n\nfunction openApp() {\n    router.push(`/studio/${props.app.ID}`);\n}\nfunction updateApp() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)), { closable: true, title: \"Update Application\" }, {app: props.app});\n}\nfunction deleteApp() {\n    executor.execute(async () => {\n        await objectUseCase.deleteObject(props.app.ID);\n    }, { loading: true });\n}\n\n</script>\n<template>\n    <div class=\"block\" v-if=\"block\"></div>\n    <div class=\"card each-card\" :style=\"appColor\">\n        <div class=\"card-body\">\n            <div class=\"row\">\n                <div class=\"row\">\n                    <div class=\"col-3 appLogoDiv \">\n                        <!-- <img  src='../../../../images/is-bankasi-logo.jpg' class=\"card-logo\"> -->\n                        <div v-if=\"!app.logo\" class='mdi mdi-image-outline card-logo nonLogo'></div>\n                        <img v-if=\"app.logo\" :src='app.logo' class=\"card-logo\">\n                    </div>\n                    <div class=\"col-8\">\n                        <p class=\"card-title\">{{ app.name }}</p>\n                        <p class=\"card-description\">{{  app.description? app.description:'-' }}</p>\n                        <p v-if=\"app.createDate\" class=\"card-createdInfo\">{{ app.createdBy }} : {{ new Intl.DateTimeFormat(\"tr-TR\",options).format(new Date(app.createDate))}}</p>\n                        <!-- <p class=\"card-info\">{{app.createdBy}}  {{app.createDate}}</p> -->\n                    </div>\n                    <div class=\"col-1 p-0 dot-icon\">\n                        <i class=\"mdi mdi-dots-vertical vertical-dot-icon dotMenu ms-4\" id=\"dropdownMenuButton1\"\n                            data-bs-toggle=\"dropdown\" aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"></i>\n                        <ul class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                            <li><a class=\"dropdown-item\" @click='updateApp()'><i\n                                        class=\"mdi mdi-pencil-outline\"></i>Edit</a></li>\n                            <li><a class=\"dropdown-item list-group-item-danger\" @click=\"deleteApp()\"><i\n                                        class=\"mdi mdi-delete-outline\"></i>Delete</a></li>\n                        </ul>\n                    </div>\n                </div>\n\n                <div class=\"row modulesInfoDiv\" >\n                    <div class=\"col\">\n                        <p class=\"text-center m-0 card-mpt\" :style=\"appTextColor\">Modules</p>\n                        <p class=\"text-center card-numbers\">{{ app!.details!.moduleCount }}</p>\n                    </div>\n                    <div class=\"col dashes\">\n                        <p class=\"text-center m-0 card-mpt\" :style=\"appTextColor\">Pages</p>\n                        <p class=\"text-center card-numbers\">{{ app!.details!.pageCount }}</p>\n                    </div>\n                    <div class=\"col\">\n                        <p class=\"text-center m-0 card-mpt\" :style=\"appTextColor\">Templates</p>\n                        <p class=\"text-center card-numbers\">{{ app!.details!.pageTemplateCount }}</p>\n                    </div>\n                </div>\n                <div class=\"row\">\n                    <p class=\"open-in-studio openApp-link\" :style=\"appTextColor\" @click=\"openApp()\">OPEN IN STUDIO <i class=\"mdi mdi-arrow-right mdi-18px\"></i> </p>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<style>\n@import url(\"../../../../styles/IDE3.css\");\n</style>\n<style scoped>\n.block {\n    background-color: white;\n    opacity: 0.8;\n    height: 246px;\n    width: 332px;\n    position: absolute;\n    z-index: 1;\n}\n.dotMenu{\n    cursor: pointer;\n}\n\n.nonLogo{\n    font-size: 36pt;\n    color: #F16C70;\n}\n.modulesInfoDiv{\n    margin-top: 20px;\n}\n.card-createdInfo{\n    color: #616161;\n    font: normal normal medium 14px/19px Roboto;\n    letter-spacing: 0px;\n    font-family: 'Roboto';\n}\n</style>"],"sourceRoot":""}]);
